<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Promotion
      </div>
      <v-spacer></v-spacer>
      <v-btn :to="{ name: 'Promotioncreate' }" color="primary" depressed elevation="2">
        <v-icon>mdi-plus</v-icon>
        Tambah</v-btn>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table :headers="headers" :items="data.data" class="elevation-1" loading-text="Loading... Please wait" :loading="data.loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.isactive`]="{ item }">
            {{ item.isactive ? 'Active' : 'Non Active' }}
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn icon color="success" @click="detail(item)">
            <v-icon small>
              mdi-eye
            </v-icon>
          </v-btn>
          <v-btn icon color="warning" @click="edit(item)">
            <v-icon small>
              mdi-lead-pencil
            </v-icon>
          </v-btn>
          <v-btn icon color="red" @click="hapus(item.id)">
            <v-icon small>
              mdi-delete
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <Detail/>
    <Edit/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
import Detail from '@/components/Promotion/Detail.vue'
import Edit from '@/components/Promotion/Edit.vue'
export default {
  name: 'PromotionPage',
  data: () => ({
    search: '',
    headers: [
      {
        text: 'Product',
        align: 'start',
        sortable: false,
        value: 'ProductID'
      },
      { text: 'Deskripsi', sortable: false, value: 'Promotion_Description' },
      { text: 'Link', sortable: false, value: 'RedirectTo' },
      { text: 'Status', sortable: false, value: 'isactive' },
      { text: 'Action', sortable: false, value: 'action' }
    ]
  }),
  components: {
    Detail, Edit
  },
  mounted: function () {
    store.dispatch('promotion/reqData')
  },
  computed: {
    ...mapState('promotion', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('promotion', [
      'edit', 'hapus', 'detail', 'closeModal'
    ])
  }
}
</script>
