<template>
  <v-row justify="center">
    <v-dialog v-model="data.dialogEdit" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="text-h5">Edit</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="12" md="12" class="justify-center">
                <center> <v-img class="justify-center" max-width="300px" :src="data.edit.ImagesPath" /></center>
                <v-file-input @change="createBase64Image" color="deep-purple accent-4" counter accept="image/*"
                  label="Upload Image" placeholder="Select your files" prepend-icon="mdi-paperclip" outlined
                  :show-size="1000">
                  <template v-slot:selection="{ index, text }">
                    <v-chip v-if="index < 2" color="deep-purple accent-4" dark label small>
                      {{ text }}
                    </v-chip>

                    <span v-else-if="index === 2" class="text-overline grey--text text--darken-3 mx-2">
                      +{{ files.length - 2 }} File(s)
                    </span>
                  </template>
                </v-file-input>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-select v-model="data.edit.ProductID" :loading="products.loading" :items="products.data"
                  item-text="FinancePurpose" item-value="FinancePurpose" label="Product" outlined></v-select>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-textarea label="Deskripsi" v-model="data.edit.Promotion_Description" outlined></v-textarea>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field label="Link" v-model="data.edit.RedirectTo" outlined></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-select v-model="data.edit.isactive" :items="items" item-text="text" item-value="value" label="Status"
                  outlined></v-select>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <div>
          <v-spacer></v-spacer>
          <v-btn color="secondary" @click="closeModalEdit()">
            Close
          </v-btn>
          <v-btn color="primary" :loading="data.loading" @click="update()">
            Update
          </v-btn>
        </div>
      </v-card>
    </v-dialog>
  </v-row>
</template>
<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: 'PromotionDetail',
  data: () => ({
    items: [
      {
        text: 'Active', value: '1'
      },
      {
        text: 'No Active', value: '0'
      }
    ]
  }),
  computed: {
    ...mapState('promotion', {
      data: (state) => state
    }),
    ...mapState('product', {
      products: (state) => state
    })
  },
  methods: {
    ...mapActions('promotion', [
      'closeModalEdit', 'update'
    ]),
    createBase64Image: function (FileObject) {
      const reader = new FileReader()
      reader.onload = (event) => {
        this.data.edit.ImagesPath = event.target.result
      }
      reader.readAsDataURL(FileObject)
    }
  }
}
</script>
